import { createAsyncThunk } from '@reduxjs/toolkit';
import { APIRoute } from '../../const';
import { dropToken, saveToken } from '../../services/token';
import { adaptEmployeeEducationToClient, adaptEmployeeEducationsToClient, adaptEmployeeLanguages, adaptEmployeeToClient, adaptPersonalDataToClient } from '../../adapters/employees';
import { generatePath } from 'react-router-dom';
export const checkAuthAction = createAsyncThunk('employees/checkAuth', async (_arg, { extra: api }) => {
    const { data } = await api.get(APIRoute.Login);
    return adaptEmployeeToClient(data.employee);
});
export const loginAction = createAsyncThunk('employees/login', async ({ body, errorHandler }, { extra: api, rejectWithValue }) => {
    try {
        const { data } = await api.post(APIRoute.Login, body);
        saveToken(data.token);
        return adaptEmployeeToClient(data.employee);
    }
    catch (err) {
        let error = err;
        if (!error.response) {
            throw err;
        }
        errorHandler(error.response.data);
        return rejectWithValue(error.response.data);
    }
});
export const logoutAction = createAsyncThunk('auth/logout', async (_arg, { extra: api }) => {
    await api.delete(APIRoute.Logout);
    dropToken();
});
export const fetchEmployeePersonalData = createAsyncThunk('employees/fetchEmployeePersonalData', async ({ employeeId }, { extra: api }) => {
    const { data } = await api.get(generatePath(APIRoute.EmployeePersonalData, { employeeId }));
    return adaptPersonalDataToClient(data);
});
export const updateEmployeeAvatar = createAsyncThunk('employees/updateEmployeeAvatar', async ({ form, employeeId, onSuccess }, { extra: api }) => {
    form.append('_method', 'put');
    const { data } = await api.post(generatePath(APIRoute.EmployeeAvatar, { employeeId }), form);
    onSuccess(adaptEmployeeToClient(data));
});
export const deleteEmployeeAvatar = createAsyncThunk('employees/deleteEmployeeAvatar', async ({ employeeId, onSuccess }, { extra: api }) => {
    const { data } = await api.delete(generatePath(APIRoute.EmployeeAvatar, { employeeId }));
    onSuccess(adaptEmployeeToClient(data));
});
export const fetchEmployeeById = createAsyncThunk('employees/fetchEmployeeById', async ({ employeeId }, { extra: api }) => {
    const { data } = await api.get(generatePath(APIRoute.Employee, { employeeId }));
    return adaptEmployeeToClient(data);
});
export const updateEmployeeAction = createAsyncThunk('employees/update', async ({ form, employeeId, errorHandler, onSuccess }, { extra: api, rejectWithValue }) => {
    try {
        form.append('_method', 'put');
        const { data } = await api.post(generatePath(APIRoute.Employee, { employeeId }), form);
        onSuccess && onSuccess();
        return adaptEmployeeToClient(data);
    }
    catch (err) {
        let error = err;
        if (!error.response) {
            throw err;
        }
        errorHandler(error.response.data);
        return rejectWithValue(error.response.data);
    }
});
