export const createOrUpdateEmployeeLanguagesAction = createAsyncThunk('employees/createOrUpdateEmployeeLanguages', async ({ employeeId, languages, onSuccess }, { extra: api }) => {
    const { data } = await api.post(generatePath(APIRoute.EmployeeLanguages, { employeeId }), { languages });
    onSuccess && onSuccess();
    if (!data) {
        return null;
    }
    return adaptEmployeeLanguages(data);
});
